import React, { useState } from "react";
import image from "../images/design-desk.jpeg";
import { Document, Page } from 'react-pdf';

const imageAltText = "desktop with books and laptop"
const projectList = [
  {
    title: "    ",
    summary:
        "       ",
    url: "",
    pdfFile:"../assets/comizoa.pdf"
  },
  {
    title: " REC ",
    summary:
        "REC       , REC  Nice   ",
    url: "https://www.kwater.or.kr/rec/wq/index.jsp?w2xPath=/rec/ui/rec/po/main.xml",
  },
  {
    title: "KISTI  AI    ",
    summary:
        "       ,       ",
    url: "https://aida.kisti.re.kr/",
  },
  {
    title: "  ",
    summary:
        "   openAPI Gateway     .",
    url: "https://odp.airport.kr/apiPortal/main",
  },
];

const Portfolio = () => {
  const [numPages, setNumPages] = useState();
  const [pageNumber, setPageNumber] = useState(1);
  const [selectedProject, setSelectedProject] = useState(null);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  const handleProjectClick = (project) => {
    setSelectedProject(project);
  };

  const closeModal = () => {
    setSelectedProject(null);
  };

  return (
      <section className="padding" id="portfolio">
        <h2 style={{ textAlign: "center" }}>Portfolio</h2>
        <div style={{ display: "flex", flexDirection: "row", paddingTop: "3rem" }}>
          <div style={{ maxWidth: "40%", alignSelf: "center" }}>
            <img
                src={image}
                style={{ height: "90%", width: "100%", objectFit: "cover" }}
                alt={imageAltText}
            />
          </div>
          <div className="container">
            {projectList.map((project) => (
                <div className="box" key={project.title}>
                  <button
                      onClick={() => handleProjectClick(project)}
                      style={{ textAlign: "left", background: "none", border: "none", padding: 0 }}
                  >
                    <h3 style={{cursor:"pointer"}}>{project.title}</h3>
                  </button>
                  <p className="small">{project.summary}</p>
                </div>
            ))}
          </div>
        </div>

        {/* Modal for showing detailed project information */}
        {selectedProject && (
            <div
                style={{
                  position: "fixed",
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                  backgroundColor: "rgba(0, 0, 0, 0.5)",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
            >
              <div
                  style={{
                    backgroundColor: "white",
                    padding: "2rem",
                    borderRadius: "8px",
                    maxWidth: "600px",
                    width: "100%",
                    maxHeight: "80vh",
                    overflowY: "auto",
                  }}
              >
                <h3>{selectedProject.title}</h3>
                <p>{selectedProject.summary}</p>
                {selectedProject.pdfFile && (
                <div>
                  <Document file={selectedProject.pdfFile} onLoadSuccess={onDocumentLoadSuccess}>
                    <Page pageNumber={pageNumber} />
                  </Document>
                  <p>
                    Page {pageNumber} of {numPages}
                  </p>
                </div>
                )}
                {selectedProject.url && (
                    <a
                        href={selectedProject.url}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "blue", textDecoration: "underline" }}
                    >
                      
                    </a>
                )}

                <button
                    onClick={closeModal}
                    style={{
                      float: "right",
                      display: "block",
                      marginTop: "1rem",
                      backgroundColor: "gray",
                      color: "white",
                      border: "none",
                      padding: "0.5rem",
                      cursor: "pointer",
                    }}
                >
                  Close
                </button>
              </div>
            </div>
        )}
      </section>
  );
};

export default Portfolio;
